.products {
  width: 100%;
}

.list {
  display: flex;
  flex-wrap: wrap;
  max-width: 64rem;
  margin-left: auto;
  justify-content: space-evenly;
}
.flex {
  display: flex;
}

.buttons {
  display: flex;
  width: 14.5rem;
  margin: 3rem auto;
  justify-content: space-between;
  align-items: center;
}
.pageNumButton {
  width: 2.18rem;
  height: 2.18rem;
  background: linear-gradient(
    90deg,
    rgba(255, 198, 80, 0.3) 0%,
    rgba(254, 202, 110, 0.3) 97.25%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: #3f4e65;
}
.rightButton,
.leftButton {
  border: solid #ffcf7a;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 4px;
  width: 20px;
  height: 20px;
}
.rightButton {
  transform: rotate(-45deg);
}
.leftButton {
  transform: rotate(135deg);
}
.rightButton button,
.leftButton button {
  width: 100%;
  height: 100%;
  padding: 1rem;
}

@media (max-width: 771px) {
  .list {
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
  }
}
